<template>
    <div id="PersonajeComponents">
      <div class="container" id="componentePersonajes">
        <div class="row" id="contenedorTabla">
          <h1>Personaje </h1>
          <table id="tabla">
            <tr>
              <td class="titulos">Nombre </td>
            </tr>
            <tr>
              <td class="valores">{{ nombre }}</td>
            </tr>
            <tr>
              <td class="titulos">Genero</td>
            </tr>
            <tr>
              <td class="valores">{{ genero}}</td>
            </tr>
            <tr>
              <td class="titulos">Altura </td>
            </tr>
            <tr>
              <td class="valores">{{ altura }}</td>
            </tr>
            <tr>
              <td class="titulos">Numero de Peliculas </td>
            </tr>
            <tr>
              <td class="valores">{{ numeroPeliculas }}</td>
            </tr>
          </table>
          
        </div><router-link to="/personajes">Volver</router-link>
      </div>

    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    name: 'PersonajeComponents',
    props:['id'],
    data() {
      return {
        nombre:'',
        genero:'',
        altura:'',
        numeroPeliculas:'',
      
      };
    },
    methods:{
      consultarApiPersonaje: function(identificador){
        let url= `https://swapi.dev/api/people/`+identificador;
        axios.get(url)
          .then(respuesta =>{
            this.nombre=respuesta.data.name;
            this.genero=respuesta.data.gender;
            this.altura=respuesta.data.height;
            this.numeroPeliculas=respuesta.data.films.lenght;
          })
          .catch(error =>{
            console.log(error);
          });
    },
    },
    created(){
      this.consultarApiPersonaje(this.id);
    }
   
  };
  </script>
<style scoped>
#PersonajeComponents{
  background-color: black;
}
#componentePersonajes{
  width: 60%;
    background-color: black;
    color: white;
}
.titulos{
  background-color: grey;
  color: yellow;
}
#contenedorTabla{
  justify-content: center;
}
#tabla{
  text-align: center;
}

</style>