<template>
    <div id="PersonajesView">
      <h1 id="cajaTitulo">Personajes de Star Wars</h1> <br> 
      <button @click="buscarPersonajes">Cargar m√°s personajes</button><br> <br>   
        <div class="container">
            <div class="row" >
                <div id="cajaPersonajes" class="col-md-4" v-for="(personaje , index) in personajes" :key="index">
                    <a v-on:click="infoPersonaje(index)">{{ personaje.name }}</a>
                </div>
            </div> 
      </div>
    </div>
  </template>
  
  <script>
   import axios from 'axios';
  
  export default {
    name: 'PersonajesView',
    data() {
      return {
        personajes:[],
        contadorPagina:1,
      };
   },
   methods:{
    ConsumoApiStarWars: function(pagina){
      //funcion dinamica
      let url = `https://swapi.dev/api/people?page=`+ pagina;
      axios.get(url)
          .then(respuesta =>{
           
            for(let i=0; i<respuesta.data.results.length; i++){
                this.personajes.push(respuesta.data.results[i]);
            }
            
          })
          .catch(error =>{
            console.log(error);
          });
    },
    buscarPersonajes: function(){
        this.contadorPagina+=1;
        this.ConsumoApiStarWars(this.contadorPagina);
    },
    infoPersonaje: function(identificador){
      let identificadorAjustado = identificador+1;
      this.$router.push(`/personaje/${identificadorAjustado}`);
    }
   },
    created(){
        this.ConsumoApiStarWars(1);
        
   }
  };
  </script>
<style scoped>
#PersonajesView{
    background-color: rgb(12, 12, 12);
    color: yellow;
    padding-bottom: 20px;
}
#cajaTitulo{
    padding-bottom: 20px;
    
}
#cajaPersonajes{
    border: solid 2px white;
    color: white;
    margin-bottom: 2px;

}
</style>